// 
const _FIX = 'hs_dorm_';

var storage = {
	// 
	set(key,value) {
		localStorage.setItem(_FIX + key, JSON.stringify(value));
	},
	// 
	get(key) {
		return JSON.parse(localStorage.getItem(_FIX + key))
	},
	// 
	remove(key) {
		localStorage.removeItem(_FIX + key);
	},
	// 
	clear() {
		localStorage.clear();
	}
}

export default storage;


